.App {
height: 100vh;
width: 100vw;
box-sizing: border-box;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
min-width: 360px;
}

@media screen and (max-width: 784px){
  .App {
    justify-content: initial;
  }
}


@keyframes fadeIn {
  0% {opacity: 0;}
  100% {opacity: 1;}
}